/* @import url('https://fonts.googleapis.com/css2?family=Lato:ital@1&display=swap'); */
div.noPadding {
    padding-left: 0;
    padding-right: 0;
}

div.noMargin {
    margin-left: 0;
    margin-right: 0;
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    /* font-family: 'Lato', sans-serif; */
}

.productos, .contacto{
    font-family: 'Mali', sans-serif;
    display: flex;
    height: 90vh;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
}


.grilla-4 {
  display: flex;
  flex-wrap: nowrap;
  padding-top: 0rem;
  padding-bottom: 0rem;
  justify-content: space-around;
}

.arreglo {
  margin-bottom: 30px;
  width: 18%;
  /* display: inline-block; */
}

.center-foot {
  text-align: center;
  padding-top: 2rem;
}

.expand-center {
  margin: auto;
  width: 100%;
  text-align: center;
}

.detalles.card-body {
  padding: 0.5rem;
  font-family: "Mali", sans-serif;
  font-size: 1.5rem;
  color: white;
  text-align: center;
  background-color: rgb(144, 96, 150);
}

.zoom {
  max-width: 100%;
  transition: all 0.3s;
}

.zoom:hover {
  transform: scale(1.1);
  opacity: 0.8;
}

.zoom-restraint {
  overflow: hidden;
  cursor: pointer;
}

.zoom .card-img {
  position: relative;
}

.arreglo .card-img, .card-img-top {
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
}

.arreglo .card-img, .card-img-bottom {
  border-bottom-right-radius: 0px; 
  border-bottom-left-radius: 0px;
}

.titles {
    font-family: 'Mali', sans-serif;
    font-size: 1.8rem;
    padding-bottom: 4rem;
    padding-top: 6rem;
    font-weight: bold;
  }

.fas { color: rgba(194, 146, 189, 0.705); } 

.color{
  color: rgb(108, 83, 165);
}
.center {
  margin: auto;
  width: 40%;
  text-align: center;
}

html{
  font-size: 14px;
}
.contain {
  overflow: hidden;
  padding-bottom: 1rem;
  padding-top: 1rem;
  text-align: center;
}


footer {
  padding: 2.2rem;
}

.gradient {
  width: 100%;
  margin: 0;
  color: #fff;
  background: #654ea3; 
  background: linear-gradient(
    to right,
    #eaafc8,
    #654ea3
  ); 
}

section a, p, h3{
    font-size: 1.2rem;
    color: #fff;
    font-family: "Open Sans", sans-serif;
    text-decoration: none;

  }

  section a:hover{
    color: slateblue;
    text-decoration: none;
  }

  section h3{
    display: block;
    font-size: 1.17em;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
    font-weight: bold;
    
  }

  footer p {
    margin: 0;
    color:  #2f3541;
  }

  .grilla-3 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr; /* 30px 30px 30px or repeat(3, 1fr)*/
    grid-gap: 10px;
    padding-top: 2rem;
    padding-bottom: 3rem;
    padding-right: 0rem;
  }
   
  
  
.fondo{
  background-image: linear-gradient(rgba(0,0,0,0.4), rgba(156, 98, 98, 0.4)),url(/Oveluna2.0/static/media/header_bg.f4f9391c.JPG);
  height: 85vh;
  background-size: cover;
  text-align: center;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.fondo h1{
  display: inline;
  font-size: 5.5rem;
}

.fondo h1, h4 {
  color: #fff;
  font-family: "Mali", sans-serif;
  min-height: 23vh;
}


.navbar{
  background-color: #343a40;
  height: 90px;
}

.nav-menu{
  margin-top: -55px; 
  line-height: 0px;
  width: 100%;
  text-align: end;
  /* display: none; */
  position: absolute;
  
}
.menu-bars {
 display: none;
}

.nav-text{
  display: inline; 
}

.nav-text a:hover{
  border-radius: 40px;
  background: #d7bde2;
  color: #fff;
  text-decoration: none;
}

.nav-text a{
  padding-top: 30px;
  padding-right: 20px;
  padding-bottom: 30px;
  padding-left: 30px;
  transition: all 600ms ease;
  color: #fff;
  font-family: "Mali", sans-serif;
  font-weight: bold;
  font-size: 1.8rem;
}
span {
  margin-left: 8px;
}

@media screen and (max-width: 720px) {    
    .menu-bars {
      display: flex;
      margin-left: 1rem;
      font-size: 3rem;
      background: none;
    }

    .nav-menu{
      margin: auto;
      font-family: 'Mali', sans-serif;
      z-index: 1; 
      background-color: #25282c;
      width: 240px;
      height: 100vh;
      display: flex;
      justify-content: center;
      top: 0;
      left: -100%;
      transition: 850ms;
      position: fixed;
    }

    .nav-menu.active {
      left: 0;
      transition: 350ms;
    }

    .nav-text {
      display: flex;
      /* justify-content: flex-start; */
      align-items: center;
      padding: 8px 0px 8px 16px;
      list-style: none;
      height: 60px;
    }

    .nav-text a {
      text-decoration: none;
      color: #f5f5f5;
      font-size: 1.6rem;
      width: 95%;
      height: 100%;
      display: flex;
      align-items: center;
      padding: 0 16px;
      border-radius: 4px;
    }

    .nav-text a:hover {
      text-decoration: none;
    }

    .nav-menu-items {
      width: 100%;
    }
    
    .navbar-toggle {
      background-color: #25282c;
      width: 100%;
      height: 100px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }

    span {
      margin-left: 18px;
    }
  }
